#main-container {
  margin: 20px 0px;
  padding: 0px 30px;
  width: 100%;
}

#card-container {
  width: 100%;
  padding: 25px 0px;
  margin: 20px 0px;
  padding: 15px 30px;
}

#answer-list-container {
  padding-left: 40px;
}

#answer-container {
  margin: 40px 0px 10px 0px;
  width: 100%;
  padding: 30px;
}

.question-title {
  font-size: 30px !important;
  border-bottom: 3px solid #f47e42;
  width: 70%;
  padding-bottom: 5px;
  margin-bottom: 10px;
}

.info-container {
  width: 100%;
}

.question-markdown {
  background-color: #f1f1f1;
  border-radius: 3px;
  padding: 15px 25px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-bottom: 25px;
  border: 1px solid lightgray;
  width: 100%;
}

.question-votes {
  padding-left: 50px;
  padding-right: 60px;
}

.question-info {
  width: 100%;
}

.actions-container {
  width: 100%;
}

.actions {
  color: #f47e42;
  margin: 0px 10px;
}

.answer {
  margin-right: 5px;
}

.edit {
  margin-right: 5px;
}

.actions:hover {
  cursor: pointer;
  font-weight: bolder;
}

.record-info {
  width: 100%;
  color: #707070;
  text-align: right;
}

.record-info-answer {
  text-align: left;
}

.answer-card-right > .info-container {
  border: 1px solid lightgray;
  border-bottom-width: 0px;
  padding: 5px 10px;
  background-color: #f6f8fa;
  text-align: left;
}

.actions-container {
}

.answer-card-right > .question-markdown {
  margin-top: 0px;
  border-radius: 0px;
  width: 100%;
}

.answer-list-title {
  font-size: 24px;
  border-bottom: 2px solid #707070;
  width: 100%;
}

.answer-card-left {
  width: 12%;
  padding-left: 60px;
  padding-right: 90px;
  padding-bottom: 30px !important;
}

.answer-card-right {
  width: 90%;
}

.empty-answers-container {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 50px;
}

.actions-tags-container {
  margin: 0px 0px 5px 0px;
  width: 100%;
}


