html {
  font-size: 100%;
}

body {
  height: 100vh;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
    sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

#root {
  height: 100vh;
}

#header-and-body-wrapper {
  height: 100vh;
}

#sidebar {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
}

button:disabled {
  opacity: 0.5;
}

#main-and-sidebar-wrapper {
  overflow: hidden;
  margin: 0px;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-top: 80px;
}

.column {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

#left {
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  margin-right: 280px;
}

#right {
  position: fixed;
  right: 0px;
}

.tag {
  margin-right: 8px;
  background-color: #f78a54 !important;
  font-weight: 300;
}

.tag:hover {
  background-color: #f78a54 !important;
}

.tags-container {
  margin: 1px 0px 4px 0px !important;
}

.core-token--standard {
  border-radius: 3px !important;
}

.react-tagsinput-tag {
  background-color: #f47e42;
  border: 1px solid #f47e42;
  color: #ffffff;
}

.react-tagsinput-tag:focus {
  border-color: #d9d9d9 !important;
}

input:focus, input::-moz-selection { 
  outline: none !important;
  box-shadow: none !important;
}

input:focus, input::selection { 
  outline: none !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
}

textarea:focus {
  outline: none !important;
  border-color: #d9d9d9 !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
}

.react-tagsinput {
  background-color: #fff;
  border: 1px solid #ccc;
  overflow: hidden;
  padding-left: 5px;
  padding-top: 5px;
}
 
 .react-tagsinput--focused {
  border-color: #d9d9d9;
}
 
 .react-tagsinput-tag {
  border-radius: 2px;
  display: inline-block;
  font-family: sans-serif;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 5px;
}
 
 .react-tagsinput-remove {
  cursor: pointer;
  font-weight: bold;
}
 
 .react-tagsinput-tag a::before {
  content: " ×";
}
 
 .react-tagsinput-input {
  background: transparent;
  border: 0;
  color: #777;
  font-family: sans-serif;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 6px;
  margin-top: 1px;
  outline: none;
  padding: 5px;
  width: 80px;
}

button:disabled {
  background-color: #4D4D4D !important;
  border: 1px solid #4D4D4D !important;
}


